.move-history {
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    overflow: hidden; /* Prevents outer scrolling */
    border-radius: 8px;
    background-color: var(--background-color);
    color: var(--text-color);
    font-size: 1rem;
    text-align: center;
}

.move-history-wrapper {
    width: 100%;
    height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    font-size: 1rem;
    text-align: center;
}

.move-history h3 {
    text-align: center;
    margin-bottom: 5px;
}

.move-history table {
    width: 100%;
    border-collapse: collapse;
}

.move-history thead {
    position: sticky;
    top: 0;
    background-color: var(--text-color);
    z-index: 2;
}

.move-history tbody {
    display: block;
    height: 450px; /* Ensures tbody is scrollable */
    overflow-y: auto;
}

.move-history tr {
    display: flex;
    justify-content: space-between;
}

.move-history th {
    background-color: var(--background-color1);
    color: var(--text-color);
    font-weight: bold;
    padding: 10px;
}

.move-history th,
.move-history td {
    width: 33%;
    text-align: center;
    padding: 5px;
    border-bottom: 1px solid var(--background-color);
}


/* Fix last move visibility */
.move-history tbody tr:last-child td {
    padding-bottom: 10px;
}

.move-history table tbody tr:nth-child(even) {
    background-color: var(--background-color1); 
  }
  
  .move-history table tbody tr:nth-child(odd) {
    background-color: var(--background-color2); 
  }